*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: #AA076B;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #61045F, #AA076B);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #61045F, #AA076B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
color:white;
font-family: 'Oleo Script Swash Caps', cursive;
}

.main-head{
    width: 10%;
    margin:0 auto;
    padding: 5px;
    text-align: center;
}
.main-head h1{
    font-size: 60px;

}



.sideBtn{
position: absolute;
top: 30%;
left: 10%;
}

.sideBtn button{
    box-shadow: 0px 10px 13px -4px rgb(32, 29, 29);
    background:none;
    border:none;
    color:white;
    font-family: 'Oleo Script Swash Caps', cursive;
    padding: 12px 14px;
    font-size: 20px;
    margin: 16px 0px;
}



.score{
    font-size: 28px;
    margin: 10px 0px;
}
.card-grid{
    width: 45%;
    margin:0 auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap:12px;
    margin-top: 2px;
}



img{
    width: 100%;
    display: block;
    border:2px solid #fff;
    border-radius: 40%;
    
}